@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal {
    position: fixed;
    background-color: rgb( 0 0 0 / .8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .formulario {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 500ms;
        opacity: 0;
        border-radius: 1rem;

        legend {
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }
        input[type="text"] {
            border: 1px solid v.$gris;
        }
        .opciones {
            display: flex;
            justify-content: space-between;
            gap: 1rem;
        }
        .submit-nueva-tarea {
            @include m.boton(v.$indigo, v.$blanco);
            border-radius: .5rem;
        }
        .cerrar-modal {
            @include m.boton(v.$naranja, v.$blanco);
            border-radius: .5rem;
            text-transform: uppercase;
        }
        //Contener formulario
        @include m.telefono-mn {
            width: 95%;
            max-width: 1200px;
            margin: 0 auto;
        }

        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        } 
    }
}