@use 'base/variables' as v;
@use 'base/mixins' as m;

.restablecer {
    margin-top: 5rem;
    .uptask {
        @include m.textoDegradado(v.$rosa, v.$naranja);
    }

    input[type="submit"] {
        border-radius: 1rem;
        @include m.boton(v.$rosa, v.$blanco);
    }

    .acciones {
        a{
            color: v.$rosa;
        }
    }
}